:root {

  /* -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- */

  /* Commonly used for non-text elements, though not limited */

  --primary-color: #FF8E3C;
  --secondary-color: #D9376E;

  /* -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- */

  /* Commonly used for text elements and backgrounds, though not limited */

  --dark-color: #272343;
  --light-color: #EFF0F3;

  /* -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- */

  /* BRIGHTNESS SETTINGS */

  --hover: 110%;
  --active: 105%;

 /* -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- */

}

/* -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- */

.material-symbols-outlined {

  margin-top: 5px;
  margin-right: 24px;

font-variation-settings:
'FILL' 1,
'wght' 400,
'GRAD' 0,
'opsz' 48
}

/* -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- */

body {
  background-color: var(--light-color);
  font-family: 'Barlow', sans-serif;
  color: var(--dark-color);
  margin: 25px;
}

button {
  width: 88px;
  height: 32px;

  border: solid 1px;
  margin-right: 40px;
}

button:hover {
  filter: brightness(var(--hover));
}

button:active {
  filter: brightness(var(--active));
}

hr {
  border: dashed 1px;
  margin-top: 25px;
  margin-bottom: 10px;
}

a:link {
  color: var(--primary-color);
}

a:visited {
  color: var(--secondary-color);
}

a:hover {
  filter: brightness(var(--hover));
}

a:active {
  filter: brightness(var(--active));
} 

/* -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- */

.icon {

  margin-top: 20px;
  margin-right: 25px;
  margin-bottom: 15px;

}

.color-preview {
  float: left;
  margin-right: 10px;

  width: 34px;
  height: 34px;

  border: solid 1px;
  border-color: var(--dark-color);
}

.color-selector {
  width: 88px;
  height: 32px;

  border: solid 1px;
  margin-right: 10px;
}

.text-input {
  width: 1090px;
  
  padding: 10px;

  font-family: 'Barlow', sans-serif;
  color: var(--dark-color);

  border-radius: 0;
  border: solid var(--dark-color) 1px;
}

.text-input:focus {
  border-color: var(--primary-color);
  border-block-color: var(--primary-color);
  outline: none;
}

.text-input::selection {
  background-color: var(--secondary-color);
  color: var(--light-color);
}

.text-input::-moz-selection {
  background-color: var(--secondary-color);
  color: var(--light-color);
}

.preview-box {
  padding-top: 5px;
  padding-left: 25px;
  padding-right: 25px;
  padding-bottom: 5px;

  width: 1062px;
}

.underline {
  text-decoration: underline;
}

/* -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- */

.light {
  background-color: var(--light-color);
}

.primary {
  background-color: var(--primary-color);
}

.secondary {
  background-color: var(--secondary-color);
}

.dark {
  background-color: var(--dark-color);
}

/* -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- */

.light-border {
  border-color: var(--light-color);
}

.primary-border {
  border-color: var(--primary-color);
}

.secondary-border {
  border-color: var(--secondary-color);
}

.dark-border {
  border-color: var(--dark-color);
}

/* -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- */

.light-text {
  color: var(--light-color);
}

.primary-text {
  color: var(--primary-color);
}

.secondary-text {
  color: var(--secondary-color);
}

.dark-text {
  color: var(--dark-color);
}

/* -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- */

.light-fill {
  fill: var(--light-color);
}

.primary-fill {
  fill: var(--primary-color);
}

.secondary-fill {
  fill: var(--secondary-color);
}

.dark-fill {
  fill: var(--dark-color);
}

/* -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- */